<template>
  <div id="app">
    <h1>Google图书搜索器</h1>
    <p style="color: #999">Maybe you need a VPN?</p>
    <el-container>
      <el-header>
        <el-input type="text" v-model="bookName" style="width: 200px" />
        <el-button @click="getBook" round style="margin-left: 10px">Search</el-button>
      </el-header>
      <el-main>
        <ul>
          <li v-for="(item, index) in items" :key="index">
            <el-card class="box-card">
              <div slot="header" class="clearfix">
                <span>{{ item.volumeInfo.title }}</span>
              </div>
              <div class="text item liBox">
                <div class="fontBox">
                  作者:<span v-for="(author, index) in item.volumeInfo.authors" :key="index" style="padding: 5px">{{ author }}</span>
                  <br />
                  语言:<span>{{ item.volumeInfo.language }}</span>
                  <br />
                  发行时间: <span>{{ item.volumeInfo.publishedDate }}</span>
                  <br />
                  简介: <span v-if="item.volumeInfo.description">{{ item.volumeInfo.description }}</span>
                  <span v-else>There is currently no introduction available</span>
                </div>
                <div class="imgBox">
                  <img v-if="item.volumeInfo.imageLinks && item.volumeInfo.imageLinks.thumbnail" :src="item.volumeInfo.imageLinks.thumbnail" alt="" />
                  <span v-else>No Image Available</span>
                </div>
              </div>
            </el-card>
          </li>
        </ul>
      </el-main>
    </el-container>
  </div>
</template>

<script>
import axios from 'axios'

export default {
  name: 'App',
  data() {
    return {
      bookName: '',
      items: []
    }
  },
  methods: {
    getBook() {
      axios
        .get(`https://www.googleapis.com/books/v1/volumes?q=${this.bookName}`)
        .then(res => {
          this.items = res.data.items
          console.log(this.items)
          console.log(this.items[0].volumeInfo.imageLinks.thumbnail)
        })
        .catch(err => {
          console.log(err)
        })
    }
  }
}
</script>

